import {Icon} from 'src/components';

/**
 *  id + parentId  id + children
 * 
 *  id
 *  parentId  children
 *  title
 *  path
 *
 * @param userId
 * @returns {Promise<[{
 * id: string,
 * parentId: string,
 * icon: string,
 * title: string,
 * basePath: string,
 * path: string,
 * target: string,
 * order: number,
 * }]>}
 * @description
 *  basePath: 
 *  path: 
 *  target: iframeiframe
 *  order: 
 */
export default async function getMenus(userId) {
    // userId  token 
    // const menus = await ajax.get('/menus', {userId}).then(res => {
    //     return (res || []);
    // });

    const menus = [
        {id: 'system', title: '', order: 900},
        {id: 'user', parentId: 'system', title: '', path: '/users', order: 900},
        {id: 'role', parentId: 'system', title: '', path: '/roles', order: 900},
        {id: 'menu', parentId: 'system', title: '', path: '/menus', order: 900},
        {id: 'demo', title: '', basePath: '/demo', order: 850},
        {id: 'layout', parentId: 'demo', title: '', path: '/layout', order: 900},
        {id: 'modal', parentId: 'demo', title: '', path: '/modal', order: 900},
        {id: 'query-bar', parentId: 'demo', title: '', path: '/query-bar', order: 900},

        {id: 'other-site', title: '', order: 800},
        {id: 'baidu', parentId: 'other-site', title: '', path: 'https://baidu.com', target: 'iframe', order: 2000},
        {id: 'antDesign', parentId: 'other-site', title: 'Ant Design ', path: 'https://ant-design.gitee.io', order: 2000},
        {id: 'document', parentId: 'other-site', title: '', path: 'http://shubin.wang/docs', target: '_blank', order: 1200},

        {id: 'example', title: '', order: 600},
        {id: 'page404', parentId: 'example', title: '404', path: '/404', order: 700},
        {id: 'table-editable', parentId: 'example', title: '', path: '/example/table-editable', order: 600},

        {id: 'level', basePath: '/demo', parentId: 'example', title: '', order: 500},
        {id: 'level1', parentId: 'level', path: '/1', title: '1', order: 500},
        {id: 'level11', parentId: 'level', path: '/2', title: '11', order: 500},
        {id: 'level2', parentId: 'level11', path: '/3', title: '11', order: 500},

        {id: 'baidu-family', basePath: 'http://baidu.com', title: '', order: 400},
        {id: 'baidu-zhidao', parentId: 'baidu-family', title: ''},
        {id: 'baidu-buzhidao', parentId: 'baidu-zhidao', title: '', path: '/buzhidao', target: 'iframe'},
        {id: 'code-key', parentId: 'baidu-family', title: '', type: '2', code: 'ADD_USER', path: '/buzhidaoa', target: 'iframe'},
    ];

    return loopMenus(menus);
}

/**
 * 
 * @param nodes
 * @param basePath
 */
function loopMenus(nodes, basePath) {
    //  order 
    nodes.sort((a, b) => b.order - a.order);

    nodes.forEach(item => {
        let {icon, path, target, children} = item;

        // basePath
        const parentNode = nodes.find(it => it.id === item.parentId);
        if (parentNode) basePath = parentNode.basePath;

        // bashPath
        if (basePath && !('basePath' in item)) item.basePath = basePath;

        // 
        if (basePath && path && (!path.startsWith('http') || !path.startsWith('//'))) {
            item.path = path = `${basePath}${path}`;
        }

        // 
        if (icon) item.icon = <Icon type={icon}/>;

        // targetiframe
        if (target === 'iframe') {
            //  : iFrame
            item.path = `/iframe_page_/${encodeURIComponent(path)}`;
            Reflect.deleteProperty(item, 'target');
        }

        if (children?.length) loopMenus(children, item.basePath);
    });

    return nodes;
}
